*, *:after, *:before {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background: #1C180D;
  font-family: "Press Start 2P", cursive;
}

.container {
  width: 1140px;
  margin: 0px auto;
}

.App {
  padding: 40px;
}
.App .box {
  width: 50px;
  height: 50px;
  border: 1px solid rgba(255, 255, 255, 0.3137254902);
}

